/* ==========
CUSTOM LIGHTBOX
========== */

.lightbox-screen {
    display: none;
    position: fixed;
    z-index: 10;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, .5);
    background: linear-gradient(to bottom, rgba(255, 255, 255, .5), rgba(255, 255, 255, 1) 80%);

    &.active {
        display: flex;
    }
}

.lightbox-content {
    display: none;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    position: fixed;
    z-index: 20;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 80vw;
    max-width: 120rem;
    height: 80vh;
    margin-top: 10vh;
    margin-bottom: 10vh;
    border-radius: .6rem;
    background-color: #fff;

    @include shadow03;

    &.active {
        display: flex;
    }

    figure {
        overflow-y: scroll;
        border-radius: .6rem;
        width: 100%;
        height: auto;
    }

    .lightbox-image {
        display: block;
        width: 100%;
    }

    .btn-close {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        padding: .8rem 1.2rem;
        color: #fff;
        line-height: 1;
        background-color: #000;

        &:hover {
            background-color: #23caa3;
        }

        .fa-times-circle {
            display: inline-block;
            height: 1.4rem;
            margin-right: .8rem;
        }
    }
}

.lightboxScroll {
    overflow: hidden;
}

@media screen and (max-width: 1400px) {
    .lightbox-content {
        width: 90vw;
        max-width: unset;
    }
}

@media screen and (max-width: 680px) {
    .lightbox-content {
        width: 96vw;
        max-width: unset;
    }
}