/* ==========
Iglu search product page
========== */

$bar-color: #3dee82;
$back-color: #162d4a;


.search-insights {
    @include flex-column;
    justify-content: space-between;

    width: calc(100vw - 16rem);
    margin: 0 8rem 4rem 8rem;
    padding: 8rem;
    background-color: $back-color;
    position: relative;

    .content {
        width: 100%;
        position: relative;
        color: #fff;
    }

    .content-row {
        @include flex-row;
        justify-content: space-between;
        max-width: 120rem;

        .column {
            width: 48%;
        }
    }

    .title {
        @include folio-card-title;
        width: 100%;
        max-width: 48rem;
        color: #fff;

        &:before {
            background-color: $bar-color;
        }
    }

    figure {
        width: 100%;
        overflow-x: scroll;
    }

    table {
        color: #fff;
        width: 100%;

        th {
            border: .2rem solid $back-color;
            padding: .8rem 1.6rem;
            white-space: nowrap;
        }

        td {
            border: .2rem solid $back-color;
            padding: .8rem 1.6rem;
            background-color: rgba(255, 255, 255, .1);
            white-space: nowrap;

            &:first-child {
                white-space: wrap;
            }
        }
    }

    .fa-desktop,
    .fa-tablet-alt,
    .fa-mobile-alt,
    .fa-check-circle,
    .fa-minus-circle {
        fill: $bar-color;
        height: 1.4rem;
        margin: -.4rem .6rem 0 0;
    }

    .fa-minus-circle {
        fill: #ccc;
    }

}


// RESPONSIVE
@media screen and (max-height: 800px) {

    .search-insights {
        height: auto;
    }
}

@media screen and (max-width: 1400px) {

    .search-insights {
        width: calc(100vw - 8rem);
        margin: 0 4rem 4rem 4rem;

        figure {
            box-shadow: inset -28px -2px 30px -35px rgba(0, 0, 0, 0.75);
        }
    }
}

@media screen and (max-width: 1100px) {

    .search-insights {
        margin: 0 4rem 4rem 4rem;
        padding: 4rem;
    }
}

@media screen and (max-width: 960px) {
    .search-insights {
        align-items: center;

        .content {
            max-width: 48rem;
        }

        .content-row {
            @include flex-column;

            .column {
                width: 100%;
            }
        }
    }
}

@media screen and (max-width: 680px) {

    .search-insights {
        width: calc(100vw - 3.2rem);
        margin: 0 1.6rem 4rem 1.6rem;
        padding-right: 2.4rem;
        padding-left: 2.4rem;

        .imagery {
            padding-left: 1.6rem;
            padding-right: 1.6rem;

            .desktop {
                width: 100%;
                margin: 0;
            }

            .mobile {
                display: none;
            }

        }

    }
}