/* ==========
CASE STUDY IGLU SEARCH Intro & mixins
========== */

.card-search-intro {
    overflow: hidden;

    @include flex-column;
    justify-content: center;

    width: calc(100vw - 16rem);
    height: calc(100vh - 10rem);
    margin: 0 8rem 4rem 8rem;
    padding: 8rem;
    background: #f0eeed url(../img/igl-src-sketch-wires03.jpg) no-repeat bottom right;
    background-size: auto 100%;
    position: relative;

    .logo {
        position: absolute;
        top: 4.8rem;
        left: 0;
        padding: 1.6rem 2.4rem;
        border-radius: 0 .4rem .4rem 0;
        background-color: #fff;

        svg {
            width: 20rem;
        }
    }

    .content-row {
        @include flex-row;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }

    .content {
        width: 100%;
        max-width: 60rem;
        padding: 4rem 8rem 4rem 0;
        position: relative;
    }

    .title {
        @include folio-card-title;

        span {
            white-space: nowrap;
        }

        &:before {
            background-color: #0097d2;
        }
    }

    .link-inline {
        &:before {
            background-color: #0097d2;
            opacity: .5;
        }

        &:hover:before {
            opacity: .2;
        }
    }

    .imagery {
        height: 100%;
        width: 50%;
        position: relative;

        @include flex-column;
        align-items: flex-start;
    }

    .desktop {
        position: relative;
        width: 48rem;
        border-radius: .4rem;
        overflow: hidden;
        margin: 0 1.6rem 1.2rem;
        @include shadow03;
    }

    .mobile {
        width: 24rem;
        border-radius: .4rem;
        overflow: hidden;
        margin: 1.2rem 1.6rem;
        @include shadow03;
    }

    figcaption {
        margin: 0 2.4rem;
        width: 100%;
        max-width: 40rem;
        line-height: 1.2;
    }

    .icon-scroll-down {
        left: 50%;
        border-color: #0097d2;

        &:before {
            background-color: #0097d2;
        }
    }
}


// RESPONSIVE
@media screen and (max-height: 800px) {

    .card-search-intro {
        height: auto;

        .icon-scroll-down {
            display: none;
        }
    }
}

@media screen and (max-width: 1400px) {

    .card-search-intro {
        width: calc(100vw - 8rem);
        margin: 0 4rem 4rem 4rem;
        padding: 8rem 4rem;

        .mobile {
            right: -2rem;
        }
    }
}

@media screen and (max-width: 1100px) {

    .card-search-intro {
        height: auto;
        background-image: none;

        .logo {
            left: auto;
            top: 2rem;
            right: 0;
            border-radius: .4rem 0 0 .4rem;

            svg {
                width: 16rem;
            }
        }

        .content-row {
            @include flex-column;
        }

        .content {
            padding-right: 0;
        }

        .imagery {
            @include flex-row;
            flex-wrap: wrap;
            justify-content: center;
            width: 100%;
        }

        .desktop {
            margin: 0 2.4rem 0 0;
        }

        figcaption {
            margin-top: 1.6rem;
        }

        .icon-scroll-down {
            display: none;
        }

    }
}

@media screen and (max-width: 960px) {

    .card-search-intro {
        .imagery {
            .mobile {
                top: 8rem;
                right: -4rem;
                bottom: auto;
            }
        }
    }
}

@media screen and (max-width: 680px) {

    .card-search-intro {
        background-image: none;
        width: calc(100vw - 3.2rem);
        margin: 0 1.6rem 4rem 1.6rem;
        padding-top: 8rem;

        .content {
            padding: 2.4rem 0;
        }

        .desktop {
            margin: 0 0 2.4rem 0;
        }

        .mobile {
            margin: 0 0 2.4rem 0;
        }

        figcaption {
            margin: 0;
        }
    }
}

@media screen and (max-width: 375px) {

    .card-search-intro {
        padding-right: 2.4rem;
        padding-left: 2.4rem;

    }

}