/* ==========
CASE STUDY search progressive
========== */

$slide:#44486a;

.search-progressive {
    @include flex-column;
    justify-content: space-between;

    width: calc(100vw - 16rem);
    margin: 4rem 8rem;
    padding: 8rem;
    color: #fff;
    background-color: $slide;
    position: relative;

    .content {
        width: 100%;
        max-width: 50rem;
        position: relative;
    }

    .title {
        @include folio-card-title;

        &:before {
            background-color: #66dd15;
        }
    }

    .imagery {
        position: absolute;
        top: 0;
        right: 0;
        width: 40%;
        height: 100%;
        overflow: hidden;
        @include flex-column;
        justify-content: center;
        align-items: center;

        &:after,
        &:before {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            width: 100%;
            background-color: $slide;
            opacity: .6;
            content: '';
        }

        &:after {
            width: 8rem;
            opacity: .6;
        }

        img {
            height: 140%;
            width: auto;
        }

        figcaption {
            position: absolute;
            right: 2.4rem;
            bottom: 2.4rem;
            width: 60%;
            line-height: 1.3;
        }
    }

}


// RESPONSIVE
@media screen and (max-width: 1400px) {
    .search-progressive {
        width: calc(100vw - 8rem);
        margin: 4rem;
        padding-right: 4rem;
        padding-left: 4rem;
    }
}

@media screen and (max-width: 1100px) {
    .search-progressive {
        align-items: center;
        padding: 0;

        .content {
            max-width: 60rem;
            padding: 4rem;
            margin: 0;
        }

        .imagery {
            position: relative;
            top: auto;
            right: auto;
            width: 100%;
            height: 32rem;
            justify-content: flex-end;

            &:after {
                width: 100%;
                height: 8rem;
            }

            img {
                height: auto;
                width: 100%;
            }

            figcaption {
                position: absolute;
                right: auto;
                left: 2.4rem;
                bottom: 2.4rem;
                width: 100%;
                max-width: 40rem;
                z-index: 2;
            }
        }
    }
}

@media screen and (max-width: 680px) {

    .search-progressive {
        width: calc(100vw - 3.2rem);
        margin-right: 1.6rem;
        margin-left: 1.6rem;

        .imagery {
            figcaption {
                right: 2.4rem;
                left: 2.4rem;
                bottom: 2.4rem;
                width: calc(100% - 4.8rem);
                max-width: unset;
            }
        }
    }
}

@media screen and (max-width: 375px) {

    .search-progressive {

        .content {
            padding-right: 2.4rem;
            padding-left: 2.4rem;
        }

    }

}