/*--------------------------
RESUME / CV
--------------------------*/

.cv-card {
    padding-bottom: 4rem;
}

.cv-layout {
    @include flex-row;
    max-width: 88rem;
    width: 100%;
    margin: 0 auto 4rem;
    padding: 2.4rem;
}

.cv-experience {
    width: 70%;
    padding-right: 8rem;
    position: relative;

    &:before {
        position: absolute;
        top: 80px;
        bottom: 0;
        left: -20px;
        width: 1px;
        background-color: rgba(2, 176, 243, .2);
        content: '';
    }
}

.cv-quals {
    width: 30%;

    .cv-subtitle {
        color: $cyan;
    }
}

.cv-heading {
    color: #fff;
    text-align: center;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: .5rem;
    margin: 1.6rem 0;
    padding: 4rem 0;
    background: rgb(57, 216, 143);
    background: linear-gradient(201deg, rgba(57, 216, 143, 1) 0%, rgba(2, 176, 243, 1) 100%);
}

.cv-title {
    font-weight: 600;
    font-size: 2.4rem;
    line-height: 1.1;
    margin: 4rem 0;
}

.cv-subtitle {
    font-weight: normal;
    font-style: normal;
    font-size: 1.6rem;
    margin: 0 0 1.2rem 0;
    position: relative;

    .dates {
        color: $cyan;
        font-weight: 600;
        min-width: 10rem;
        text-align: right;
        position: absolute;
        left: -14rem;
        top: 0;
        display: block;

        &:before {
            position: absolute;
            top: 7px;
            right: -2.4rem;
            width: 8px;
            height: 8px;
            border: 2px solid #02b0f3;
            border-radius: 50%;
            background-color: #fff;
            content: '';
        }
    }

    .link-inline {
        font-weight: 600;

        &:before {
            bottom: -.6rem;
            background-color: $cyan;
            opacity: .5;
        }

        &:hover:before {
            opacity: .1;
            transform: scaleY(1.5);
        }
    }

    .fa-map-marker-alt {
        height: 1.4rem;
        fill: $turq;
        margin: -.2rem .8rem 0 0;
    }

    .location {
        color: #888;
        margin-left: .8rem;
        padding-left: 2rem;
    }
}

.cv-sectiontitle {
    font-size: 1.4rem;
    font-weight: 500;
    margin-top: 2rem;

    small {
        font-weight: 500;
        color: $cyan;
        text-transform: uppercase;
        letter-spacing: .1rem;
        display: block;
    }
}

.cv-list {
    margin-top: .5em;
    list-style: none;
    padding: 0;

    .item {
        font-size: 1.4rem;
        line-height: 1.3;
        background-size: auto 1.6rem;
        padding-left: 1.4rem;
        margin-bottom: .8rem;
        position: relative;

        &:before {
            display: none; // reset general bar style
        }
    }

    .fa-chevron-right {
        position: absolute;
        top: .4rem;
        left: 0;
        height: 1rem;
        fill: $turq;
    }
}

.cv-description {
    p {
        font-size: 1.4rem;
        margin: 0 0 .8rem 0;
    }
}




/*--------------------------
RESPONSIVE
--------------------------*/

@media only screen and (max-width:1200px) {

    /* mobile navigation */
    .cv-layout {
        max-width: 72rem;
    }

}

@media only screen and (max-width: 960px) {

    .cv-layout {
        max-width: none;
        padding: 2.4rem 4rem;
    }

    .cv-subtitle {
        .dates {
            position: relative;
            top: auto;
            left: auto;
            right: auto;
            text-align: left;
            margin-bottom: .4rem;

            &::before {
                left: -2.4rem;
                right: auto;
            }
        }
    }

}

@media only screen and (max-width: 620px) {

    .cv-layout {
        flex-direction: column;
    }

    .cv-experience {
        width: 100%;
        padding-right: 0;
    }

    .cv-quals {
        width: 100%;
    }

    .cv-subtitle {
        .location {
            font-size: 1.2rem;
            padding-left: 1.2rem;
            margin-left: 0;
        }
    }

}