/* ==========
UTILITY CLASSES
========== */

// reset
* {
    box-sizing: border-box;
}

figure {
    margin: 0;
    padding: 0;
}

img {
    display: block;
    width: 100%;
}

// device - tablet
.desktop,
.tablet {
    border-radius: 1rem;
    background-color: #2b2a40;
    box-shadow: 0.8rem 0.8rem 1.6rem 0 rgba(00, 00, 00, 0.2);

    .screen {
        overflow: hidden;
        position: absolute;
        top: 1rem;
        right: 1rem;
        bottom: 1rem;
        left: 1rem;
        background-color: #fff;
        border-radius: .4rem;
    }

    img {
        width: 100%;
        display: block;
    }
}

// device - mobile
.mobile {
    border-radius: .6rem;
    background-color: #2b2a40;
    box-shadow: 0.4rem 0.4rem 1.2rem 0 rgba(00, 00, 00, 0.2);

    .screen {
        overflow: hidden;
        position: absolute;
        top: .8rem;
        right: .8rem;
        bottom: 2.4rem;
        left: .8rem;
        background-color: #fff;
        border-radius: .2rem;
    }

    img {
        width: 100%;
        display: block;
    }
}

// change cursor for vertical scroll
.scroll-v,
.screen.scroll-v {
    overflow-y: scroll;

    &:hover {
        cursor: url(../img/ui-cursor-vertical-scroll.svg), move;
    }
}