/* ==========
CASE STUDY IEP final
========== */

.card-iep-final {
    @include flex-column;
    justify-content: center;

    width: calc(100vw - 16rem);
    margin: 4rem 8rem;
    padding: 4rem 8rem;
    color: #fff;
    background-color: #002342;
    position: relative;

    .content-row {
        @include flex-row;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }

    .content {
        width: 50%;
        max-width: 60rem;
        padding: 0 8rem 0 0;
        position: relative;
    }

    .title {
        @include folio-card-title;

        &:before {
            background-color: #66dd15;
        }
    }

    .video {
        @include shadow02;
        width: 50%;
        border-radius: .4rem;
    }
}


// RESPONSIVE
@media screen and (max-width: 1400px) {

    .card-iep-final {
        width: calc(100vw - 8rem);
        margin: 0 4rem 4rem 4rem;
        padding: 4rem;
    }
}

@media screen and (max-width: 960px) {

    .card-iep-final {
        height: auto;

        .content-row {
            flex-direction: column;
            align-items: center;
        }

        .content {
            width: 100%;
            max-width: 60rem;
            padding-right: 0;
        }

        .video {
            width: 100%;
            max-width: 60rem;
        }
    }
}

@media screen and (max-width: 680px) {

    .card-iep-final {
        width: calc(100vw - 3.2rem);
        margin: 0 1.6rem 4rem 1.6rem;
    }
}

@media screen and (max-width: 375px) {

    .card-iep-final {
        padding-right: 2.4rem;
        padding-left: 2.4rem;
    }

}