/* ==========
CASE STUDY IGLU SEARCH Intro & mixins
========== */

$bar-color: #00abff;
$back-color: #ced6df;

.search-card-designs {
    @include flex-row;
    justify-content: space-between;

    width: calc(100vw - 16rem);
    margin: 4rem 8rem;
    padding: 0 4rem 0 8rem;
    background-color: $back-color;
    position: relative;

    &:before {
        position: absolute;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1;
        height: 4rem;
        content: '';
        background: linear-gradient(to top,
                $back-color,
                rgba(255, 255, 255, 0))
    }

    .content {
        width: 48%;
        max-width: 60rem;
        padding: 8rem 8rem 8rem 0;
        position: relative;
    }

    .title {
        @include folio-card-title;

        &:before {
            background-color: $bar-color;
        }
    }

    .desktop-imagery {
        width: calc(50% - 2.4rem);
        height: 100%;
        position: absolute;
        top: 0;
        right: 2.4rem;
        bottom: 0;

        figure {
            padding: 0 2.4rem;
        }

        img {
            margin-bottom: 2.4rem;

            &:first-child {
                margin-top: 8rem;
            }

            &:last-child {
                margin-bottom: 8rem;
            }
        }
    }

}


// RESPONSIVE
@media screen and (max-height: 800px) {

    .search-card-designs {
        height: auto;
    }
}

@media screen and (max-width: 1400px) {

    .search-card-designs {
        width: calc(100vw - 8rem);
        margin: 0 4rem 4rem 4rem;
        padding: 4rem;
    }
}

@media screen and (max-width: 1100px) {

    .search-card-designs {
        .content {
            width: 50%;
            padding: 2.4rem 2.4rem 2.4rem 0;
        }

        .desktop-imagery {
            right: 0;
        }

    }
}

@media screen and (max-width: 860px) {
    .search-card-designs {
        @include flex-column;
        align-items: center;

        &:before {
            display: none;
        }

        .content {
            width: 100%;
            max-width: 48rem;
            padding: 0;
        }

        .desktop-imagery {
            position: relative;
            width: 100%;
            max-width: 56rem;
            max-height: 56rem;
            padding-top: 2.4rem;
            border: 1px solid #fff;
            border-radius: .4rem;

            img {
                &:first-child {
                    margin-top: 0;
                }
            }

        }

    }
}

@media screen and (max-width: 680px) {

    .search-card-designs {
        background-image: none;
        width: calc(100vw - 3.2rem);
        margin: 0 1.6rem 4rem 1.6rem;

        .desktop-imagery {
            figure {
                padding: .8rem;
            }
        }
    }
}

@media screen and (max-width: 375px) {

    .search-card-designs {
        padding-right: 2.4rem;
        padding-left: 2.4rem;
    }

}