/* ==========
Iglu search product page
========== */

$bar-color: #59c4c4;
$back-color: #e1eef2;

.search-product-page {
    @include flex-column;
    justify-content: space-between;

    width: calc(100vw - 16rem);
    margin: 0 8rem 1.6rem 8rem;
    padding: 8rem 8rem 0 8rem;
    // background-color: $back-color;
    position: relative;

    .content {
        width: 100%;
        position: relative;
    }

    .content-row {
        @include flex-row;
        justify-content: space-between;
        max-width: 104rem;

        .column {
            width: 46%;
        }
    }

    .title {
        @include folio-card-title;
        max-width: 60rem;

        &:before {
            background-color: $bar-color;
        }
    }

    .imagery {
        @include flex-row;
        justify-content: space-between;
        align-items: flex-start;
        width: 100%;
        height: 60rem;
        padding: 4rem;
        margin-bottom: 4rem;
        background-color: $back-color;
        overflow-y: scroll;
        border-bottom: .2rem solid $back-color;

        .desktop {
            width: 68%;
        }

        .mobile {
            width: 28%;
            background-color: unset;
        }
    }

}


// RESPONSIVE
@media screen and (max-height: 800px) {

    .search-product-page {
        height: auto;
    }
}

@media screen and (max-width: 1400px) {

    .search-product-page {
        width: calc(100vw - 8rem);
        margin: 0 4rem;
        padding: 4rem;
    }
}

@media screen and (max-width: 1100px) {

    .search-product-page {
        @include flex-column;
        align-items: center;

        .content {
            width: 100%;
            padding: 0;
        }

        .imagery {
            top: auto;
            width: 100%;
            height: 40rem;
            border-top: 2px solid #fff;

            .desktop {
                right: auto;
            }

        }
    }
}

@media screen and (max-width: 860px) {
    .search-product-page {
        padding: 0;

        .content {
            justify-content: center;
            align-items: center;
            width: 100%;
            max-width: 48rem;
            padding: 0;
        }

        .content-row {
            @include flex-column;

            .column {
                width: 100%;
            }
        }
    }
}

@media screen and (max-width: 680px) {

    .search-product-page {
        width: calc(100vw - 3.2rem);
        margin: 0 1.6rem 4rem 1.6rem;
        padding-right: 2.4rem;
        padding-left: 2.4rem;

        .imagery {
            padding-left: 1.6rem;
            padding-right: 1.6rem;

            .desktop {
                width: 100%;
                margin: 0;
            }

            .mobile {
                display: none;
            }

        }

    }
}