/* ==========
CASE STUDY IEP Collaboration slide
========== */

.card-iep-collab {
  @include flex-column;
  justify-content: center;

  width: calc(100vw - 16rem);
  margin: 4rem 8rem;
  padding: 4rem 8rem;
  background-color: #d5d2d2;
  position: relative;

  .content-row {
    @include flex-row;
    justify-content: space-between;
    align-items: flex-start;
    align-content: flex-start;
    width: 100%;
  }

  .content {
    width: 40%;
    max-width: 60rem;
    padding-right: 8rem;
    position: relative;
  }

  .title {
    @include folio-card-title;
    max-width: 60rem;

    &:before {
      background-color: #d2a700;
    }
  }

  .wire-group {
    @include flex-row;
    justify-content: space-between;
    align-items: flex-start;
    width: 60%;

    figure {
      width: 48%;
    }

    .wire {
      border-radius: .4rem;
      overflow: scroll;
      width: 100%;
      height: auto;
      @include shadow01;
      display: block;
    }

    figcaption {
      margin-top: 1.2rem;
      line-height: 1.2;
    }
  }

}

// RESPONSIVE

@media screen and (max-width: 1400px) {
  .card-iep-collab {
    width: calc(100vw - 8rem);
    margin: 4rem;
    padding: 4rem;
  }
}

@media screen and (max-width: 1100px) {

  .card-iep-collab {
    .content {
      width: 60%;
    }

    .wire-group {
      width: 40%;

      figure {
        width: 100%;

        &:last-child {
          display: none;
        }
      }

      figcaption {
        margin-top: 1.2rem;
        line-height: 1.2;
      }
    }
  }
}

@media screen and (max-width: 960px) {

  .card-iep-collab {

    .content-row {
      @include flex-column;
      justify-content: flex-start;
      align-items: center;
      width: 100%;
    }

    .content {
      width: 100%;
      max-width: 60rem;
      padding: 0;
      margin: 0;
    }

    .wire-group {
      width: 100%;

      figure {
        width: 48%;

        &:last-child {
          display: block;
        }
      }
    }
  }
}

@media screen and (max-width: 860px) {
  .card-iep-collab {

    .content-row {
      @include flex-column;
      justify-content: flex-start;
      align-items: center;
      width: 100%;
    }

    .wire-group {
      figure {
        width: 100%;

        &:last-child {
          display: none;
        }
      }
    }
  }
}

@media screen and (max-width: 680px) {
  .card-iep-collab {
    width: calc(100vw - 3.2rem);
    margin: 4rem 1.6rem;
  }
}

@media screen and (max-width: 480px) {

  .card-iep-collab {
    padding-right: 2.4rem;
    padding-left: 2.4rem;

    .title {
      font-size: 3.2rem;
    }

  }
}