/* ==========
VARIABLES AND MIXINS
========== */

// Color palette:
$primary-1: #171b31;
$cyan: #02b0f3;
$turq: #39d88f;


// Typography
@mixin smooth {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

// Layout
@mixin flex-column {
    display: flex;
    flex-direction: column;
}

@mixin flex-row {
    display: flex;
    flex-direction: row;
}


// Transitions
@mixin transition02 {
    transition: all .2s ease-out;
}


// Shadows
@mixin shadow01 {
    box-shadow: 0.2rem 0.2rem 0.8rem 0 rgba(00, 00, 00, 0.2);
}

@mixin shadow02 {
    box-shadow: 0.4rem 0.4rem 2.8rem 1.2rem rgba(00, 00, 00, 0.4);
}

@mixin shadow03 {
    box-shadow: 0.4rem 0.4rem 2.8rem 1.2rem rgba(00, 00, 00, 0.1);
}