/* ==========
CASE STUDY IGLU SEARCH user flow
========== */

$bar-color: #59c4c4;
$back-color: #e1eef2;

.search-user-flow {
    @include flex-row;
    justify-content: space-between;

    width: calc(100vw - 16rem);
    margin: 0 8rem 1.6rem 8rem;
    padding: 8rem;
    background-color: $back-color;
    position: relative;

    &:before {
        position: absolute;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1;
        height: 4rem;
        content: '';
        background: linear-gradient(to top,
                $back-color,
                rgba(255, 255, 255, 0))
    }

    .content {
        width: 40%;
        max-width: 48rem;
        position: relative;
    }

    .title {
        @include folio-card-title;

        &:before {
            background-color: $bar-color;
        }
    }

    .imagery {
        @include flex-row;
        justify-content: space-between;
        align-items: flex-start;
        width: 60%;
        height: 100%;
        padding: 4rem;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        overflow-y: scroll;

        .desktop {
            width: 68%;
        }

        .mobile {
            width: 28%;
            background-color: unset;
        }
    }

}


// RESPONSIVE
@media screen and (max-height: 800px) {

    .search-user-flow {
        height: auto;
    }
}

@media screen and (max-width: 1400px) {

    .search-user-flow {
        width: calc(100vw - 8rem);
        margin: 0 4rem 4rem 4rem;
        padding: 4rem;
    }
}

@media screen and (max-width: 1100px) {

    .search-user-flow {
        @include flex-column;
        align-items: center;

        .content {
            width: 100%;
            padding: 0 0 36rem 0;
        }

        .imagery {
            top: auto;
            width: 100%;
            height: 40rem;
            border-top: 2px solid #fff;

            .desktop {
                right: auto;
            }

        }
    }
}

@media screen and (max-width: 860px) {
    .search-user-flow {

        .content {
            width: 100%;
            max-width: 48rem;
            padding: 0 0 36rem 0;
        }

    }
}

@media screen and (max-width: 680px) {

    .search-user-flow {
        background-image: none;
        width: calc(100vw - 3.2rem);
        margin: 0 1.6rem 4rem 1.6rem;

        .imagery {

            .desktop {
                width: 100%;
                margin: 0;
            }

            .mobile {
                display: none;
            }

        }

    }
}

@media screen and (max-width: 375px) {

    .search-user-flow {
        padding-right: 2.4rem;
        padding-left: 2.4rem;

        .imagery {
            padding: 2.4rem .8rem;
        }
    }

}