/* ==========
Iglu search templates
========== */

.search-template {
    @include flex-column;
    justify-content: center;

    width: calc(100vw - 8rem);
    margin: 0 0 4rem 8rem;
    padding: 2.4rem 8rem;
    position: relative;

    .content-row {
        @include flex-row;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }

    .content {
        width: 50%;
        max-width: 60rem;
        padding: 4rem 8rem 4rem 0;
        position: relative;
    }

    .title {
        @include folio-card-title;

        span {
            white-space: nowrap;
        }

        &:before {
            background-color: #0097d2;
        }
    }

    .link-inline {
        &:before {
            background-color: #0097d2;
            opacity: .5;
        }

        &:hover:before {
            opacity: .2;
        }
    }

    .imagery {
        width: 50%;
        height: 100%;
        position: relative;
    }

    .desktop {
        position: relative;
        width: 100%;
        height: auto;
        padding: 1rem;
        margin-bottom: 8rem;

        .screen {
            display: block;
            width: 100%;
            position: relative;
            top: auto;
            right: auto;
            bottom: auto;
            left: auto;
        }
    }

    .mobile {
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: 3;
        width: 20rem;
        height: 36rem;

        &.v1 {
            right: 24rem;
        }
    }

}


// RESPONSIVE
@media screen and (max-height: 800px) {

    .search-template {
        height: auto;

    }
}

@media screen and (max-width: 1400px) {

    .search-template {
        width: calc(100vw - 4rem);
        margin: 0 0 4rem 4rem;
        padding: 4rem;

        justify-content: flex-start;
        align-items: flex-start;

        .content-row {
            align-items: flex-start;
        }

        .content {
            width: 40%;
            padding-top: 0;
            padding-bottom: 0;
        }

        .imagery {
            width: 60%;
        }

        .mobile {
            right: -2rem;
        }
    }
}

@media screen and (max-width: 1100px) {

    .search-template {
        margin: 0;

        .content-row {
            @include flex-column;
            align-items: center;
        }

        .content {
            width: 100%;
            padding-right: 0;
        }

        .imagery {
            width: 100%;
            max-width: 80rem;
            margin-top: 2.4rem;
        }

    }
}

@media screen and (max-width: 680px) {

    .search-template {
        width: calc(100vw - 3.2rem);
        margin: 0 1.6rem 4rem 1.6rem;
        padding-right: 2.4rem;
        padding-left: 2.4rem;

        .imagery {
            .desktop {
                margin-bottom: 0;
            }

            .mobile {
                display: none;
            }
        }
    }
}