/* ==========
LANDING PAGE - Iglu search case study link
========== */

// IEP
.card-search-landing {
    @include flex-row;
    justify-content: space-between;
    align-items: center;

    width: calc(100vw - 16rem);
    height: 80rem;
    margin: 4rem 8rem;
    padding: 4rem 8rem;

    background: rgb(220, 208, 231);
    background: linear-gradient(180deg, #ebefff 0%, #b5cae1 100%);
    position: relative;

    .content {
        width: 40%;
        padding-top: 2.4rem;
        position: relative;

        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            height: 0.8rem;
            width: 7.2rem;
            background-color: #89499b;
        }
    }

    .title {
        font-size: 4.8rem;
        line-height: 1.1;
        margin-top: 0;
        margin-bottom: 3.2rem;

        span {
            white-space: nowrap;
        }
    }

    .para {
        font-size: 1.8rem;
        line-height: 1.4;
    }

    .link {
        font-weight: bold;

        &:after {
            background-color: #89499b;
        }

        &:hover {
            .fa-chevron-right {
                fill: #89499b;
            }
        }

        .fa-chevron-right {
            position: absolute;
            top: 1.8rem;
            right: 0;
            height: 1rem;

            @include transition02;
        }
    }

    .link-inline {

        &:before {
            background-color: rgb(137, 73, 155);
            opacity: .6;
        }

        &:hover:before {
            opacity: .2;
        }
    }

    .btn-row {
        margin-top: 4rem;
    }

    .logos {
        position: absolute;
        top: 4.8rem;
        right: 0;
        padding: 1.6rem 2.4rem;
        border-radius: .4rem 0 0 .4rem;
        background-color: #fff;

        svg {
            width: 20rem;
        }
    }

    .imagery {
        height: 100%;
        width: 50%;
        position: relative;
    }

    .tablet {
        position: absolute;
        top: 10rem;
        right: 0rem;
        z-index: 2;
        width: 60rem;
        height: 40rem;
    }

    .mobile {
        position: absolute;
        right: 12rem;
        bottom: 8rem;
        z-index: 3;
        width: 18rem;
        height: 32rem;
    }

    .thumb {
        position: absolute;
        bottom: 8rem;
        right: 32rem;
        z-index: 1;
        border-radius: 0.4rem;
        width: 32rem;
        height: auto;
        background-color: #fff;
        border: .8rem solid #fff;
        border-radius: .8rem;
        transform: rotate(4deg);
    }
}


// RESPONSIVE


@media screen and (max-width: 1400px) {

    .card-search-landing {
        width: calc(100vw - 8rem);
        margin: 4rem;
    }
}

@media screen and (max-width: 1300px) {

    .card-search-landing {
        padding-right: 4rem;
        padding-left: 4rem;

        .content {
            width: 36vw;
        }

        .tablet {
            width: 52rem;
            height: 38rem;
        }

        .mobile {
            right: 6rem;
        }

        .thumb {
            right: 20rem;
        }
    }

}

@media screen and (max-width: 1140px) {

    .card-search-landing {
        padding: 4rem;
    }

}

@media screen and (max-width: 1100px) {

    .card-search-landing {
        height: auto;
        min-height: unset;
        padding-top: 8rem;
        padding-bottom: 50rem;
        @include flex-column;

        .logos {
            top: 2rem;
        }

        .content {
            width: 100%;
            max-width: 60rem;
        }

        .title {
            font-size: 4rem;
            margin-top: 0;
        }

        .btn-row {
            margin: 2rem 0;
        }

        .imagery {
            width: 100%;
        }

        .tablet {
            top: 0;
            right: 50%;
            transform: translateX(50%);
        }

        .mobile {
            bottom: auto;
            top: 16rem;
        }

        .thumb {
            bottom: auto;
            right: auto;
            left: 8rem;
            top: 26rem;
            width: 24rem;
        }

    }
}

@media screen and (max-width: 960px) {

    .card-search-landing {
        .mobile {
            right: 0;
        }

        .thumb {
            left: 0;
        }
    }
}


@media screen and (max-width: 680px) {

    .card-search-landing {
        width: calc(100vw - 3.2rem);
        margin: 4rem 1.6rem;
        padding-bottom: 4rem;

        .logos {

            svg {
                width: 14rem;
            }
        }

        .imagery {
            display: none;
        }
    }
}

@media screen and (max-width: 480px) {

    .card-search-landing {
        padding-right: 1.6rem;
        padding-left: 1.6rem;

        .logos {
            top: 0;
            right: 50%;
            transform: translateX(50%);
            padding-top: .4rem;
            border-radius: 0 0 .2rem .2rem;
        }
    }
}